.our-advantages {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: relative;
	height: fit-content;
	width: var(--prc-100);
	&__advantages-block {
		margin: var(--px-66) var(--px-159) var(--px-66) var(--px-160);
		z-index: 2;
		overflow: hidden;
		max-width: 1200px;
	}
	&__title {
		margin-bottom: var(--px-76);
		margin-left: var(--px-42);
		color: var(--white--base--100);
		font-size: var(--px-50);
		font-weight: 600;
		text-transform: uppercase;
	}
	&__blocks {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		gap: var(--px-36);
		position: relative;
		height: fit-content;
	}
	&__button {
		padding: var(--px-29) var(--px-101);
		text-transform: uppercase;
		font-size: var(--px-20);
		font-weight: 700;
	}
	&__card {
		position: absolute;
		z-index: 1;
		left: var(--prc--37);
		bottom: var(--prc--50);
	}
}
